.Menu {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 300;
  background-color: var(--main-font-color);
  color: var(--nav-active-font-color);

  -webkit-transform: translateY(-200%);

          transform: translateY(-200%);
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

.Menu-content {
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Menu-open {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.Menu-close {
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 2rem;
  padding-bottom: 2rem;
  font-size: var(--nav-font-size);
  letter-spacing: var(--main-letter-space);
  cursor: pointer;

  display: flex;
  align-items: center;
  justify-content: center;
}

.Menu-close:hover .Menu-icon {
  color: var(--active-font-color);
  transition: color 0.4s ease-in;
}

.Menu-icon {
  margin-right: 1.5rem;
  color: var(--nav-active-font-color);
}

.Menu-items {
  width: 100%;
  margin: 0;
  margin-top: 1rem;
  padding: 0;
  list-style: none;
  text-align: center;

  display: flex;
  flex-direction: column;
}

.Menu-items li {
  width: 100%;
  margin-bottom: 1rem;
}

.Menu-item {
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: var(--nav-font-size);
  letter-spacing: var(--main-letter-space);
  text-decoration: none;
  color: inherit;
}

.Menu-item:hover,
.Menu-item:active {
  color: var(--active-font-color);
}

.menu-active {
  font-weight: 600;
  color: var(--main-font-color);
  background-color: var(--shadow-color);
}

.Theme {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 200;
    height: 6rem;
    color: var(--nav-active-font-color);
    background-color: var(--main-font-color);
    box-shadow: none;
    -webkit-transform: translateX(-7rem);
            transform: translateX(-7rem);
    transition: all 0.5s ease-in-out;
    display: flex;
}

.show-theme {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    box-shadow: 8px 0px 0px 0px var(--shadow-color);
}

.Theme-options {
    height: inherit;
    width: 6rem;
    padding-left: 1rem;
    font-size: var(--nav-font-size);

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.Theme-option {
    margin: 0;
    padding: 0;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.Theme-option-select {
    box-sizing: border-box;
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 1rem;
    background-color: var(--nav-active-font-color);
}

.active-option .Theme-option-select {
    background-color: var(--active-font-color);
    border: 4px solid var(--nav-active-font-color);
}

.option-spacer {
    height: 1rem;
}

.Theme-bumper {
    height: inherit;
    padding-right: 1rem;
    padding-left: 0.5rem;
    cursor: pointer;
    
    display: flex;
    align-items: center;
    justify-content: center;
}

.Theme-icon {
    margin: 0;
    padding: 0;
    color: var(--nav-active-font-color);
}
.Music {
    position: absolute;
    top: 6rem;
    left: 0;
    z-index: 200;
    height: 6rem;
    color: var(--nav-active-font-color);
    background-color: var(--main-font-color);
    box-shadow: none;
    -webkit-transform: translateX(-18.5rem);
            transform: translateX(-18.5rem);
    transition: all 0.5s ease-in-out;
    display: flex;
}

.show-music {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    box-shadow: 8px 0px 0px 0px var(--shadow-color);
}

.Music-content {
    width: 18.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Music-track {
    margin: 0;
    margin-bottom: 0.5rem;
    padding: 0;
    font-size: var(--nav-font-size);
    color: inherit;
}

.Music-bumper {
    height: inherit;
    padding-right: 1rem;
    padding-left: 0.5rem;
    cursor: pointer;
    
    display: flex;
    align-items: center;
    justify-content: center;
}

.Music-icon {
    margin: 0;
    padding: 0;
    color: var(--nav-active-font-color);
}

.App {
  width: 100%;
  margin: 0;
  padding: 0;
  color: var(--main-font-color);
  background-color: var(--main-bg-color);

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.dark {
  --main-font-color: rgb(205, 205, 205);
  --nav-active-font-color: rgb(60, 60, 60);
  --active-font-color: rgb(72, 125, 175);
  --main-bg-color: rgb(80, 80, 80);
  --nav-hover-color: rgb(60, 60, 60);
  --shadow-color: rgb(40, 40, 40);
  --card-background-color: rgb(60, 60, 60);
}

.light {
  --main-font-color: rgb(40,40,40);
  --nav-active-font-color: rgb(240, 240, 240);
  --active-font-color: rgb(63, 137, 205);
  --main-bg-color: rgb(235, 235, 235);
  --nav-hover-color: rgb(210, 210, 210);
  --shadow-color: rgb(190, 190, 190);
  --card-background-color: #FFF;
}

/* CSS Vars */
:root {
  --main-font-color: rgb(40,40,40);
  --nav-active-font-color: rgb(240, 240, 240);
  --active-font-color: rgb(63, 137, 205);
  --nav-font-size: 1rem;
  --home-title-font-size: 3.4rem;
  --home-description-font-size: 1.2rem;
  --main-font-size: 1rem;
  --min-font-size: 0.9rem;
  --main-letter-space: 0.2rem;
  --main-bg-color: rgb(235, 235, 235);
  --nav-hover-color: rgb(210, 210, 210);
  --shadow-color: rgb(190, 190, 190);
  --card-background-color: #FFF;
}
/* END CSS Vars */

html {
  width: 100%;
}

body {
  overflow-y: scroll;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Open Sans", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

